.bg-img {
  background: url("../images/Lake.JPG") no-repeat;
  background-size: cover;

  /* Set rules to fill background */
  min-height: 1024px;
  min-width: 100%;

  /* Set up proportionate scaling */
  width: 100%;
  height: auto;

  /* Set up positioning */
  position: absolute;
  top: -87px;
  left: 0;
  z-index: 0;
}
